import mimes from "@constants/mime.json"
import Mdloader from "@components/Mdloader";
// @ts-ignore
import { SearchInput, Table } from "evergreen-ui";
import { useState, useEffect } from "react";
import { createFuzzyList } from "@utils/fuzzyScore";

const lists = createFuzzyList(mimes)

export default function () {
  const [query, setQuery] = useState("");
  const [data, setData] = useState(lists);
  useEffect(() => {
    let sorted = [];
    if (query) {
      sorted = lists.filter(function (item) {
        return (item._cachedScore = item.score(query)) >= 0.5;
      }).sort(function (a, b) {
        var as = a._cachedScore;
        var bs = b._cachedScore;
        return as > bs ? -1 : as == bs && a.i < b.i ? -1 : 1;
      }).slice(0, 10)
    } else {
      sorted = lists;
    }
    setData(sorted);
  }, [query]);
  return (
    <>
      <SearchInput
        placeholder="Typing something"
        onChange={e => {
          setQuery(e.target.value);
        }}
        value={query}
        width={"600px"}
        marginX="auto"
        height={40}
        marginBottom={10}
      />
      <table className="mimes">
        <tbody>
          <tr>
            <th className="separateColor">Extension</th>
            <th>Content-Type(Mime-Type)</th>
          </tr>
          {data.map((item, ix) => {
            return (
              <tr
                key={ix}
              >
                <td>
                  {item[0]}
                </td>
                <td >
                  <code>
                    {item[1]}
                  </code>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <style jsx>{`
        .mimes {
          font: 0.8em Monaco;
          display: block;
          text-align: center;
          border: 1px solid #ccc;
        }
        
        .mimes tr>th,.mimes tr> td {
            border: 1px solid #ddd
        }
      
        .mimes tr>th{
            border-bottom-width: 2px
        }
        
        .mimes tr:nth-of-type(odd) {
            background-color: #f9f9f9
        }
        
        .mimes >tr:hover {
            background-color: #f5f5f5
        }
      `}</style>
    </>
  );
}

